$sub-color: grey;
$main-color: black;

.selectform {
  display: flex;
  flex-direction: column;
  width: 100%;
  height: 100%;
  justify-content: flex-end;
  align-items: center;

  .selectform__form-div {
    height: 100%;
    width: 100%;
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;

    .selectform__form {
      height: 100%;
      width: 100%;
      display: flex;
      flex-direction: row;
      justify-content: space-evenly;
      align-items: center;

      .selectform__select {
        width: 100%;
        background: none;
        outline: none;
        background: transparent;
        color: white;
        font-size: 18px;
        padding: 10px 10px 10px 5px;
        display: block;

        border: none;
        border-radius: 0;
        border-bottom: 1px solid white;
        .selectform__option {
          background-color: lightgray;
          color: black;
        }
      }
    }
  }
}
