@mixin flex {
  display: flex;
  justify-content: center;
  align-items: center;
}

@mixin block-size {
  height: 40px;
  width: 33%;
}
@mixin for-l-phone-portrait {
  @media (max-width: 660px) {
    @content;
  }
}
.legend {
  width: 100%;
  height: 100%;
  @include flex;
  flex-direction: row;
  padding: 0 1rem;
  @include for-l-phone-portrait {
    flex-direction: column;
  }
  .legend__block {
    @include flex;
    margin-right: 6px;
    flex-direction: row;
    width: 30%;
    text-align: center;
    @include for-l-phone-portrait {
      width: 70%;
      padding: 0.5rem;
    }
    div {
      margin-right: 1rem;
    }

    .legend__block-color1 {
      @include block-size;
      background: #8bc4b3;
    }
    .legend__block-color2 {
      @include block-size;
      background: #d794c1;
    }
    .legend__block-color3 {
      @include block-size;
      background: #d4c09f;
    }
    .legend__span {
      width: 70%;
    }

    p {
      margin: 0;
      line-height: 1.2rem;
      font-size: 1rem;
    }
  }
}
